.woocommerce {
    #page-wrap {
        padding: 50px 0;
    }
}


.entry-content {
    .woocommerce {
        display: flex;
        flex-wrap: wrap;

        .woocommerce-table {
            @extend .table;
        }

        .u-column1.col-1 {
            @extend .col-12;
        }

        ul.products {
            li.product {
                
                .ihk-product-image {
                    position: relative;
                    overflow: hidden;
                }

                a {
                    img {
                        margin-bottom: 0;
                    }
                    &:hover {
                        img {
                            opacity: 1;
                            transform: scale(1.1);
                            transition: all 0.5s ease-in-out;
                        }
                    }
                }
                .woocommerce-loop-product__title {
                    margin-top: 0.5em;
                }
                .add_to_cart_wrapper {
                    padding: 0;
                    .button {
                        color: var(--ihk-jul)!important;
                        padding-right: 0 15px;
                        overflow: hidden;
                        max-width: unset;
                        &:after {
                            content: none;
                        }
    
                        &:hover, &:active {
                            color: var(--ihk-aug)!important;
                            overflow: visible;
                        }
                    }
                }
            }
        }

        a {
            &.button {
                border-radius: 0;
            }
        }

        .upsells {
            clear: both;
        }

        .woocommerce-tabs {
            clear: both;
        }

        .wc-tabs {
            display: none;
        }
        .woocommerce-Tabs-panel {
            > h2 {
                display: none;
            }
        }

        .woocommerce-info {
            text-align: center;
            &:before {
                position: unset;
                margin-right: 0.25em;
            }
        }

        .return-to-shop {
            text-align: center;
        }

        .quantity {
            .qty {
                border: 1px solid var(--ihk-feb);
            }
        }

        .woocommerce-MyAccount-navigation {
            ul {
                list-style: none;
                width: fit-content;
                li {
                    padding: 0 10px;
                    margin: 10px 0;
                    &.is-active {
                        a {
                            box-shadow: none;
                            &:before {
                                border-left-color: var(--ihk-feb);
                            }
                        }
                    }
                    a {
                        border-bottom: 1px dashed var(--ihk-feb);
                        width: 100%;
                        display: inline-block;
                        color: var(--ihk-gray-dark);
                        padding: 5px 0;
                        &:before {
                            content: "";
                            width: 1px;
                            height: 100%;
                            border-left: 2px solid transparent;
                            border-left-color: transparent;
                            position: absolute;
                            left: 0px;
                            top: 0;
                        }
                        &:hover, &:active {
                            color: var(--ihk-jul);
                            &:before {
                                border-left-color: var(--ihk-jul);
                            }
                        }
                    }
                    :last-child {
                        a {
                            border-bottom: none;
                        }
                    }
                }
            }
        }

        .button {
            &.single_add_to_cart_button,
            &.woocommerce-Button {
                border-radius: 0;
            }
        }

        .woocommerce-ordering {
            background-color: var(--ihk-gray-lt);
            border-radius: 0;
            select {
                border-radius: 0;
            }
        }
    }
}


.cart-main {
    .my-cart-link {
        position: relative;
        &:visited, &:active, &:hover {
            span {
                background-color: var(--ihk-jul);
            }
        }
        .cart-contents-count {
            display: none;
            width: 20px;
            height: 20px;
            background-color: var(--ihk-jul);
            color: #fff;
            font-family: $ihk-open-sans-font;
            font-size: 12px;
            line-height: 20px;
            text-align: center;
            border-radius: 50%;
            margin-left: 0;
            transition: none;
            vertical-align: text-bottom;
            &.has-count {
                display: block;
                position: absolute;
                top: 0px;
                right: 3px;
            }
        }
    }
}

@media (min-width: 768px) {
  .woocommerce-cart #coupon_code.input-text {
    width: auto !important;
  }
}


.coming-soon {
    font-family: $ihk-josefin-font !important;
    font-size: 14px !important;
    text-align: center !important;
    text-transform: uppercase! important;
}

.shop_table {
    @extend .table;
}