.ihk-dashboard .bulletins-list .bulletin-title-link {
  position: relative;
}
.ihk-dashboard .bulletins-list .bulletin-title-link:hover {
  color: var(--ihk-sep);
}
.ihk-dashboard .bulletins-list .row {
  border-bottom: 1px dashed var(--ihk-gray-dark);
  padding-bottom: 20px;
  margin-bottom: 20px;
}

#sidebar h3, #sidebar .h3 {
  margin-bottom: 0;
}

#filter-form {
  display: flex;
  flex-direction: column;
}

ul.selected-filters-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul.selected-filters-list li {
  display: inline-block;
  padding: 0;
  margin-right: 5px;
}

.remove-filter {
  color: var(--ihk-gray-dark);
  padding: 2px 3px;
}
.remove-filter:visited {
  color: var(--ihk-gray-dark);
}
.remove-filter:before {
  content: "\f14a";
  font-family: FontAwesome;
  font-size: 12px;
  color: var(--ihk-jul);
  margin-right: 3px;
}
.remove-filter:hover {
  background: var(--ihk-gray-lt);
  color: var(--ihk-africa);
}
.remove-filter:hover:before {
  content: "\f00d";
  color: var(--ihk-africa);
}

.reset-filters {
  display: block;
  color: var(--ihk-gray-dark);
  margin-bottom: 10px;
  opacity: 0;
  pointer-events: none;
}
.reset-filters:before {
  content: "\f00d";
  font-family: FontAwesome;
  font-size: 12px;
  color: var(--ihk-africa);
  margin-right: 3px;
}
.reset-filters.has-filters {
  opacity: 1;
  pointer-events: all;
}
.reset-filters:visited {
  color: var(--ihk-gray-dark);
}
.reset-filters:hover, .reset-filters:active, .reset-filters:focus {
  color: var(--ihk-africa);
}
.reset-filters:hover, .reset-filters:active {
  background-color: var(--ihk-gray-lt);
}

.clear-filters {
  color: var(--ihk-gray-dark);
  padding: 2px 3px;
}
.clear-filters:before {
  content: "\f00d";
  font-family: FontAwesome;
  font-size: 12px;
  color: var(--ihk-africa);
  margin-right: 3px;
  opacity: 0;
}
.clear-filters:hover {
  background: var(--ihk-gray-lt);
  color: var(-ihk-africa);
}
.clear-filters:hover:before {
  opacity: 1;
  color: var(--ihk-africa);
}

.filter-selector {
  position: relative;
  border: 1px solid var(--ihk-gray-dark);
  padding: 0 7px;
  margin-bottom: 10px;
  font-family: "Josefin Sans", sans-serif;
}
.filter-selector i {
  display: none;
}
.filter-selector select {
  padding: 5px 7px;
  border-color: transparent;
  font-size: 14px;
}
.filter-selector select:before {
  display: none;
}
.filter-selector select[multiple] option:before {
  content: "\f14a";
  font-family: FontAwesome;
  font-size: 12px;
  color: var(--ihk-white);
  margin-right: 3px;
}
.filter-selector select[multiple] option:checked {
  border: none;
  background-color: var(--ihk-white);
}
.filter-selector select[multiple] option:checked:before {
  color: var(--ihk-jul);
}
.filter-selector select[multiple] option:hover:before, .filter-selector select[multiple] option:active:before {
  color: var(--ihk-jul);
}
.filter-selector.toggle {
  border-color: var(--ihk-dec);
}
.filter-selector.toggle label {
  cursor: pointer;
  pointer-events: all;
}
.filter-selector.toggle span {
  display: block;
  width: 100%;
}
.filter-selector.toggle i {
  display: block;
  font-size: 1.1em;
  position: absolute;
  right: 5px;
  top: 4px;
  transition: all 0.3s ease-in-out;
  transform: rotate(180deg);
  color: var(--ihk-dec);
}
@media only screen and (min-width: 768px) and (max-width: 767px) {
  .filter-selector.toggle i {
    display: none;
  }
}
.filter-selector.toggle select {
  height: auto;
  overflow-y: clip;
  transition: height 0.3s ease-in-out;
  transition-behavior: allow-discrete;
  padding: 0;
}
.filter-selector.toggle .selected-filters-list {
  display: none;
}
.filter-selector.toggle.closed {
  border-color: var(--ihk-gray-dark);
}
.filter-selector.toggle.closed i {
  transform: rotate(0deg);
  color: var(--ihk-gray-dark);
}
.filter-selector.toggle.closed select {
  height: 0;
}
.filter-selector.toggle.closed .selected-filters-list {
  display: block;
}

