.pms-account-navigation {
  width: 30%;
  float: left;
  margin: 0;
  padding: 0;
  border: 0;
}
.pms-account-navigation ul {
  list-style: none;
  display: block !important;
  width: fit-content;
}
.pms-account-navigation ul li.pms-account-navigation-link {
  padding: 0 10px;
  margin: 10px 0;
}
.pms-account-navigation ul li.pms-account-navigation-link a {
  border-bottom: 1px dashed var(--ihk-feb) !important;
  width: 100%;
  display: inline-block;
  color: var(--ihk-gray-dark);
  padding: 5px 0;
  font-weight: 400;
}
.pms-account-navigation ul li.pms-account-navigation-link a:before {
  content: "";
  width: 1px;
  height: 100%;
  border-left: 2px solid transparent !important;
  border-left-color: transparent;
  position: absolute;
  left: 0px;
  top: 0;
}
.pms-account-navigation ul li.pms-account-navigation-link a.pms-account-navigation-link--active {
  box-shadow: none !important;
}
.pms-account-navigation ul li.pms-account-navigation-link a.pms-account-navigation-link--active:before {
  border-left-color: var(--ihk-feb) !important;
}
.pms-account-navigation ul li.pms-account-navigation-link a:hover, .pms-account-navigation ul li.pms-account-navigation-link a:active {
  color: var(--ihk-jul);
  box-shadow: none !important;
}
.pms-account-navigation ul li.pms-account-navigation-link a:hover:before, .pms-account-navigation ul li.pms-account-navigation-link a:active:before {
  border-left-color: var(--ihk-jul) !important;
}
.pms-account-navigation ul li.pms-account-navigation-link :last-child a {
  border-bottom: none;
}
@media screen and (max-width: 479px) {
  .pms-account-navigation {
    display: block;
    float: none;
    width: 100%;
  }
  .pms-account-navigation ul {
    width: 100%;
  }
  .pms-account-navigation li.pms-account-navigation-link {
    width: 100%;
  }
  .pms-account-navigation li.pms-account-navigation-link a {
    width: 100%;
  }
